/* === КНОПКА ТРИГГЕРА В ХЕДЕРЕ === */
.app-header {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 2rem;
}

.user-menu-trigger {
  background: rgba(30, 30, 30, 0.7);
  border: 1px solid #555;
  color: #aaa;
  width: 40px;
  height: 40px;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.2s;
}

.user-menu-trigger:hover {
  background: rgba(50, 50, 50, 0.9);
  color: white;
}

/* === ПОЛНОЭКРАННОЕ ОВЕРЛЕЙНОЕ МЕНЮ === */
.user-menu-overlay {
  position: fixed;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.85); /* Мрачный оверлей */
  z-index: 2000;
  display: flex;
  justify-content: flex-end;
}

.user-menu-content {
  width: 320px; /* Ширина меню */
  height: 100%;
  background: #121212; /* Очень тёмный фон */
  color: #e0e0e0;
  overflow-y: auto;
  padding: 2rem 1rem 1rem;
  position: relative;
  box-shadow: -5px 0 15px rgba(0, 0, 0, 0.5);
}

.menu-close-btn {
  position: absolute;
  top: 1rem;
  right: 1rem;
  background: none;
  border: none;
  color: #aaa;
  font-size: 2rem;
  cursor: pointer;
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.menu-close-btn:hover {
  color: white;
}

/* Стили меню */
.user-menu-content nav ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.menu-item {
  margin-bottom: 0.5rem;
  border-bottom: 1px solid #333;
}

.menu-button {
  display: block;
  width: 100%;
  padding: 14px 16px;
  text-align: left;
  background: none;
  border: none;
  cursor: pointer;
  font-size: 16px;
  color: #ccc;
  transition: color 0.2s;
}

.menu-button:hover, .menu-button:focus {
  color: white;
  background: rgba(255, 255, 255, 0.05);
}

.menu-button.admin, .menu-button.logout {
  color: #ff6b6b;
}

.menu-button.admin:hover, .menu-button.logout:hover {
  color: #ff8f8f;
}

.submenu {
  list-style: none;
  padding: 0;
  margin: 0;
  background-color: rgba(0, 0, 0, 0.2);
}

.submenu li a {
  display: block;
  padding: 12px 16px 12px 32px;
  color: #aaa;
  text-decoration: none;
  font-size: 14px;
  transition: color 0.2s;
}

.submenu li a:hover {
  color: white;
  background: rgba(255, 255, 255, 0.03);
}