/* Общий сброс и настройка */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Тёмный фон всей страницы */
body {
  margin: 0;
  font-family: 'Courier New', monospace, sans-serif;
  background: #0a0a0a; /* Глубокий чёрный */
  color: #c5c5c5; /* Мягкий серый текст для читаемости */
  line-height: 1.6;
  min-height: 100vh;
  overflow-x: hidden;
}

/* Контейнер приложения */
#root {
  max-width: 1280px;
  margin: 0 auto;
  padding: 2rem;
}

/* Стиль для навигации */
.navigation {
  margin-bottom: 2rem;
}

.navigation ul {
  list-style: none;
  display: flex;
  justify-content: center;
  gap: 2rem;
  padding: 1rem;
  background: rgba(20, 20, 20, 0.7); /* Полупрозрачный тёмный фон */
  backdrop-filter: blur(5px); /* Эффект стекла */
  border: 1px solid #333;
  border-radius: 8px;
}

.navigation a {
  text-decoration: none;
  color: #a0a0a0;
  font-size: 1.2rem;
  padding: 0.5rem 1rem;
  transition: all 0.3s ease;
  position: relative;
  text-transform: uppercase;
  letter-spacing: 2px;
}

/* Эффект наведения на ссылки: кроваво-красный */
.navigation a:hover {
  color: #ff4d4d;
  text-shadow: 0 0 10px rgba(255, 77, 77, 0.7);
}

.navigation a::after {
  content: '';
  position: absolute;
  bottom: -2px;
  left: 0;
  width: 0;
  height: 2px;
  background: #ff4d4d;
  transition: width 0.3s ease;
}

.navigation a:hover::after {
  width: 100%;
}

/* Стиль для контента страницы */
.content {
  background: rgba(15, 15, 15, 0.9);
  border: 1px solid #2a2a2a;
  border-radius: 12px;
  padding: 2rem;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.5);
}

.content h2 {
  color: #e0e0e0;
  margin-bottom: 1rem;
  font-size: 2rem;
  text-shadow: 0 0 10px rgba(224, 224, 224, 0.2);
}

.content p {
  color: #b0b0b0;
  font-size: 1.1rem;
}

/* Стиль для логотипа (мрачная аура) */
.logo {
  height: 6em;
  padding: 1.5em;
  will-change: filter;
  transition: filter 300ms;
  filter: drop-shadow(0 0 5px #000);
}

.logo:hover {
  filter: drop-shadow(0 0 2em rgba(100, 10, 30, 0.8)); /* Тёмно-красный свечение */
}

/* Карты (если будут использоваться) */
.card {
  padding: 2em;
  background: rgba(25, 25, 25, 0.8);
  border-radius: 10px;
  margin: 1rem 0;
  border: 1px solid #444;
}

/* Сноска */
.read-the-docs {
  color: #666;
  margin-top: 2rem;
  font-style: italic;
}

/* Адаптивность */
@media (max-width: 768px) {
  .navigation ul {
    flex-direction: column;
    gap: 1rem;
  }
  
  #root {
    padding: 1rem;
  }
}

/* Стиль для шапки приложения */
.app-header {
  position: fixed;
  top: 0;
  left: 0;
  padding: 1rem;
  z-index: 100;
}

/* Стиль для круглого логотипа */
.app-logo {
  height: 100px; /* Было 50px, теперь в 2 раза больше */
  width: 100px;  /* Указываем ту же ширину для идеального круга */
  object-fit: cover; /* Гарантирует, что изображение заполнит квадрат без искажений */
  border-radius: 50%; /* Делает контейнер круглым */
  opacity: 0.9;
  transition: opacity 0.3s ease, transform 0.3s ease;
  box-shadow: 0 0 0 2px rgba(255, 77, 77, 0.3); /* Тонкая красная рамка для акцента */
}

.app-logo:hover {
  opacity: 1;
  transform: scale(1.1); /* Немного увеличиваем при наведении */
  filter: drop-shadow(0 0 12px rgba(255, 77, 77, 0.7));
}

/* Контрол для чекбокса в правом верхнем углу */
.checkbox-control {
  position: fixed;
  top: 1rem;
  right: 1rem;
  z-index: 100;
  background: rgba(20, 20, 20, 0.8);
  padding: 0.5rem 1rem;
  border-radius: 20px;
  border: 1px solid #333;
}

.checkbox-control label {
  color: #a0a0a0;
  font-size: 0.9rem;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.checkbox-control input[type="checkbox"] {
  transform: scale(1.2);
  cursor: pointer;
}

.navigation .menu-checkbox label {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.5rem 1rem;
  background: rgba(30, 30, 30, 0.5);
  border-radius: 8px;
  color: #aaa;
  font-size: 0.9rem;
}

.navigation .menu-checkbox input[type="checkbox"] {
  transform: scale(1.2);
}

/* === СТИЛИ ДЛЯ СТРАНИЦЫ УПРАВЛЕНИЯ ПОЛЬЗОВАТЕЛЯМИ === */

.content {
  padding: 2rem;
  max-width: 1200px;
  margin: 0 auto;
  color: #e0e0e0;
}

.users-table {
  width: 100%;
  border-collapse: collapse;
  background-color: #1a1a1a;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
}

.users-table th,
.users-table td {
  padding: 1rem;
  text-align: left;
  border-bottom: 1px solid #333;
}

.users-table th {
  background-color: #252525;
  font-weight: 600;
  color: #aaa;
  text-transform: uppercase;
  font-size: 0.85rem;
  letter-spacing: 1px;
}

.users-table tr:last-child td {
  border-bottom: none;
}

.users-table tr:hover {
  background-color: #2a2a2a;
}

/* Стили для кнопок действий */
.users-table button {
  padding: 0.5rem 1rem;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 0.9rem;
  transition: all 0.2s ease;
  margin-right: 0.5rem;
}

.users-table button:last-child {
  margin-right: 0;
}

.users-table button:hover {
  opacity: 0.9;
  transform: translateY(-1px);
}

/* Кнопка "Добавить пользователя" */
.auth-button {
  background-color: #1976d2;
  color: white;
  padding: 0.75rem 1.5rem;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-size: 1rem;
  font-weight: 600;
  transition: background-color 0.2s;
}

.auth-button:hover {
  background-color: #1565c0;
}

/* === СТИЛИ ДЛЯ МОДАЛЬНОГО ОКНА === */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.modal-content {
  background-color: #1e1e1e;
  border: 1px solid #333;
  padding: 2rem;
  border-radius: 12px;
  max-width: 500px;
  width: 90%;
  color: #e0e0e0;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.5);
}

.modal-content h3 {
  margin-top: 0;
  margin-bottom: 1.5rem;
  color: #ccc;
}

.modal-content label {
  display: block;
  margin-bottom: 0.5rem;
  color: #aaa;
  font-weight: 500;
}

.modal-content input {
  width: 100%;
  padding: 0.75rem;
  margin-bottom: 1.5rem;
  background-color: #2a2a2a;
  border: 1px solid #444;
  border-radius: 6px;
  color: white;
  font-size: 1rem;
  box-sizing: border-box;
}

.modal-content input:focus {
  outline: none;
  border-color: #1976d2;
  box-shadow: 0 0 0 2px rgba(25, 118, 210, 0.2);
}

.modal-content button {
  padding: 0.75rem 1.5rem;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-size: 1rem;
  font-weight: 600;
  margin-right: 1rem;
}

.modal-content button[type="submit"] {
  background-color: #4caf50;
  color: white;
}

.modal-content button[type="button"] {
  background-color: #666;
  color: white;
}

/* Стили для ошибок */
.auth-error {
  color: #ff6b6b;
  background-color: rgba(255, 107, 107, 0.1);
  padding: 1rem;
  border-radius: 8px;
  margin-bottom: 1rem;
}